  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  .facebook,
  .instagram {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 50%;
  }
  .facebook {
    background-color: #4267b2;
    .facebook-login-btn {
      background-color: #4267b2;
